<template>
  <div class="role-about-detail">
    <div class="role-about-detail-item right-item-wrap">
      <base-table-panel
        title="角色管理"
        class="sortTable noTableBorder"
        :data-source="roleList"
        :right-btns="titleBtns()"
        :loading="roleLoading"
        :columns="columns()"
        :show-summary="false"
        :total="roleTotal"
        has-pagination
        :current-page="roleCurrpage"
        :page-size="rolePageSize"
        :border-style="true"
        :cell-class-name="roleCellClassName"
        @handleCurrent="handleCurrentChange"
        @handleSize="handleSizeChange"
      >
        <template slot="operate-left">
          <el-form
            :inline="true"
            label-suffix=":"
            size="mini"
            @submit.native.prevent
            class="operate-left-form"
          >
            <el-form-item>
              <el-input
                v-model="roleQueryStr"
                auto-complete="on"
                placeholder="请输入要搜索的内容"
                @keyup.enter.native="handleSearch"
                @blur="handleSearch"
              >
                <div slot="suffix" ref="inputColor1" class="backgroundChange">
                  <icon class="icon" name="icon_search" />
                </div>
              </el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="handleSearch">查询</el-button>
            </el-form-item>
          </el-form>
        </template>
      </base-table-panel>
      <roleDialog
        :roleOperType="roleOperType"
        ref="child"
        :trees="trees"
        @refresh="getRoleList"
      />
    </div>
  </div>
</template>
<script>
import { mapMutations, mapState } from 'vuex';
import roleDialog from './dialog';
import cloneDeep from 'lodash/cloneDeep';
import mixin from '../mixin';
import { roleDel, menuList } from '@api/authority.js';
import paramsCache from '@mixins/paramCache';
const paramsCacheMixin = paramsCache([
  'queryStr',
  'roleCurrpage',
  'rolePageSize',
  'roleList',
  'roleTotal',
  'roleQueryStr',
  'trees'
]);
export default {
  name: 'RoleManager',
  components: { roleDialog },
  mixins: [mixin, paramsCacheMixin],
  data() {
    return {
      dialogVisibleDel: false,
      dialogVisibleDelGroup: false,
      dataDel: {},
      checkedKeys: [],
      roleOperType: '0',
      roleId: '', // 角色ID
      titleBtns() {
        const _this = this;
        return [
          {
            title: '新增角色',
            code: 'addRole',
            icon: 'add-line',
            onClick: _this.showAddInfoAdd
          }
        ];
      },
      columns() {
        const _this = this;
        return [
          {
            prop: 'name',
            label: '角色名称',
            'header-align': 'left',
            showOverflowTooltip: true,
            'min-width': '200'
          },
          {
            prop: 'description',
            label: '角色描述',
            'header-align': 'left',
            showOverflowTooltip: true,
            'min-width': '200'
          },
          {
            prop: '',
            label: '操作',
            align: 'center',
            width: '140',
            actions: [
              {
                code: 'edit',
                text: '编辑',
                onClick: _this.showAddInfoEdit
              },
              {
                code: 'del',
                text: '删除',
                onClick: _this.setRoleDelPanel
              }
            ]
          }
        ];
      },

      trees: []
    };
  },
  watch: {
    dialogVisible(val) {
      if (!this.dialogVisible) {
        this.roleUser = {};
        this.checkedKeys = [];
        this.$refs.numberValidateForm.resetFields();
      }
    },
    dialogVisibleDel() {
      if (!this.dialogVisibleDel) {
        this.dataDel = {};
      }
    },
    dialogVisibleDelGroup() {
      if (this.dialogVisibleDelGroup !== true) {
        this.roleGroupDel = {};
      }
    }
  },
  created() {
    !this.roleList.length && this.getRoleList();
  },
  mounted() {
    !this.trees.length && this.initMenu();
  },
  beforeRouteLeave(to, from, next) {
    this.cacheParamData();
    next();
  },
  methods: {
    handleSearch() {
      // 模糊查询时重置页码为1
      this.roleCurrpage = 1;
      this.getRoleList();
    },
    handleSizeChange(val) {
      this.roleCurrpage = 1;
      this.rolePageSize = val;
      this.getRoleList();
    },
    handleCurrentChange(val) {
      this.roleCurrpage = val;
      this.getRoleList();
    },
    // 角色新增
    showAddInfoAdd() {
      this.roleOperType = '0';
      this.$refs.child.showAddInfo(false, '', this.roleOperType);
    },
    // 角色编辑
    showAddInfoEdit(row) {
      this.roleOperType = '1';
      this.$refs.child.showAddInfo(true, row, this.roleOperType);
    },
    // 删除角色提示
    setRoleDelPanel(row) {
      this.$confirm(`确定要删除${row.name}?`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).then(() => {
        roleDel(row.id).then(({ error: er }) => {
          if (!er) {
            this.$message.success(`${row.name}角色删除成功`);
            this.getRoleList();
          } else {
            this.$message.error(er.message);
          }
        });
      });
    },
    //弹窗菜单初始化
    initMenu() {
      menuList().then(({ data, error }) => {
        if (!error) {
          this.trees = data;
        } else {
          this.$message.error(`获取菜单信息失败：${error.message};`);
        }
      });
    }
  }
};
</script>
<style lang="scss">
.cellCursor {
  cursor: pointer;
}
</style>
<style lang="scss" scoped>
@import '~@assets/scss/theme.scss';
.role-about-detail {
  .showBackground {
    .backgroundChange {
      position: relative;
      width: 36px;
      height: 32px;
      border-radius: 0 4px 4px 0;
      margin-right: -5px;
      .icon {
        position: absolute;
        right: 10px !important;
        top: 9px;
        width: 14px;
        height: 14px;
        color: #cccccc;
      }
    }
  }
}
</style>
